<template>
     <div class="modal d-block my-popups dialog">
        <div class="modal-dialog">
            <div class="modal-content">
                <div class="modal-header">
                    <slot name="header">
                        <h5 class="modal-title">{{modalHeader}} Listing</h5>
                        <a title="Add Skill" href="javascript:;" class="p-action-btn text-white float-right" @click="openCredentialPopup(modalHeader)"><i class="fa fa-plus"></i><br><span>Add</span></a>
                    </slot>
                    <button type="button" @click="$emit('Close')" title="" class="close" data-original-title="Close"><em class="fa fa-times" aria-hidden="true"></em></button>
                </div>
            
            <div class="modal-body">
                <div class="row mt-0">
                    <div class="col-7 float-left pr-1 pl-0">
                        <div class="form-group">
                            <input id="search" maxlength="50" type="text" v-model="searchInput" placeholder="Search by UserName" autocomplete="off" class="form-control search">
                        </div>
                    </div>
                    <div class="col-5 float-left pl-0">
                        <div class="background-0 text-left form-check-inline">
                            <input type="hidden" id="hdnSearch" value="">
                            <a title="" id="Go" href="javascript:;" @click="searchByUsername" class="btn btn-success mr-1 px-4 hlsearch"><i class="fa fa-search"></i></a>
                            <a  href="javascript:;" title=""class="btn btn-danger clearsearchtext px-4"><i class="fa fa-refresh"></i></a>
                        </div>
                    </div>
                </div>
                <tg-list :IsShowAction="  false " ModuleName="CRM" SubModuleCode="CRM_CAMPAIGN"
                :RenderRowActionMethod=" renderActions " v-on:ActionButtonClick=" actionButtonClick "
                IdentityColumn="REF_PAGE_ID" :HeaderText=" $t('Campaign') " :listType=" ['List'] " :ListData=" CredentialList "
                :HeaderData=" Headers " :TotalRecords="totalRecords" :ListDataCallBackFunction="FetchRecord"
                :showCheckBox=" false " :SortExp=" SortExp " :SortBy=" SortBy " :NorecordfoundText=" $t('NoRecordfound') "
                v-on:LeftsearchButtonClick=" leftCommonSearch " v-on:PagerButtonClick=" pagerMethod "
                v-on:SortdataButtonClick=" sortdata ">
            </tg-list>
            </div>
        </div>
        </div>
        <modaladdsmtpVue v-if="showAddSmtpPopup" @close="close" :SendThrough="modalHeader"></modaladdsmtpVue>
        <modalSMSIntegrationVue v-if="showAddMailChimpOrSendGridPopup" @close="close" :SendThrough="modalHeader"></modalSMSIntegrationVue>
     </div>
</template>
<script>
import DataService from '../../../services/DataService';
import modaladdsmtpVue from './modaladdsmtp.vue';
import modalSMSIntegrationVue from './modalSMSIntegration.vue';
export default{
    components:{
        modaladdsmtpVue,
        modalSMSIntegrationVue
    },
    props:{
        modalHeader:  {
            type: String,
            required: true
        },
    },
    data(){
        return{
            PageSize:10,
            PageNumber:1,
            showAddSmtpPopup:false,
            CredentialList:[],
            totalRecords:0,
            showAddMailChimpOrSendGridPopup:false,
            Headers: [
                {
                    "COLUMN_NAME": "smtp_server",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP",
                    "DISPLAY_ORDER": 1
                },
                {
                    "COLUMN_NAME": "user_name",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "User Name",
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": "password",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Password",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "from_email",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Email",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": "smtp_port",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP Port",
                    "DISPLAY_ORDER": 4
                },
                {
                    "COLUMN_NAME": "smtp_security",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "SMTP_Security",
                    "DISPLAY_ORDER": 5
                }
            ],
        }
    },
    created:function(){
        var vm=this;
        vm.FetchRecord();
    },
    methods:{
        renderActions: function (row) {
           var vm=this;
        },
        FillProfileDropdown:function(){

        },
        actionButtonClick(callbackfunction, item, index, event, isChild) {
            
        },
        leftCommonSearch() {

        },
        openCredentialPopup:function(dta){
            var vm=this;
            if(dta=='MailChimp' || dta=='SendGrid'){
                vm.showAddMailChimpOrSendGridPopup=true;
            }else{
                vm.showAddSmtpPopup=true;
            }
        },
        searchByUsername() {
            var vm=this;
            return vm.CredentialList.filter(user => {
                vm.CredentialList.push(user.user_name.toLowerCase().includes(vm.searchInput.toLowerCase()));
            });
        },
        close:function(){
            var vm=this;
            vm.showAddSmtpPopup=false;
            vm.showAddMailChimpOrSendGridPopup=false;
        },
        ClosemodalManageAdditionalSMTP:function(){
            var vm=this;
            vm.showAddMailChimpOrSendGridPopup=false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchRecord();
        },
        FetchRecord:function(){
            var vm=this;
            vm.CredentialList=[];
            var url = `profileId=&sourceType=${vm.modalHeader}&pageNumber=${vm.PageNumber}&pageSize=${this.PageSize}`;
            DataService.GetEmailProfile(url).then(function (response) {
                vm.CredentialList=JSON.parse('['+response.data+']');
                vm.totalRecords=vm.CredentialList[0].Total;
            })
        }
    }
}
</script>
